@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
html, body {
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.res-height {
    height: 50%;
}

@media (min-width: 992px) { 
    .res-height {
        height: 100%;
    }
}
.vertical-scroll {
    overflow-y: scroll;
}
::-webkit-scrollbar {
    width: 6px;
    height: 8px;
}
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #7BA3C5; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #425769; 
  }
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  background-color: white;
  z-index: 10;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2);
}
.map {
  width: 100%;
  height: 100%;
}
.bi-funnel {
  margin-left: 10px;
  color: #5680A6;
}
.z-index-9 {
  z-index: 9;
}

.res-width {
  width: 35%;
}

.w-0 {
  width: 0%;
}
@media (max-width: 992px) { 
  .res-width {
    width: 90%;
  }
}
.vertical-stick {
    width: 1px;
    height: 35px;
    background-color: #dee2e6;
}
.bi-list:hover {
    opacity: 0.5;
}
.trash-counter {
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
    width: 28px;
    height: 28px;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
.sidebar svg {
    margin-top: 20px;
}
.sidebar a {
    color: #fff;
}
.bi-geo-alt-fill {
    color: #5680A6;
}
.on-map-text {
    color: #7BA3C5;
}
.on-map-text:hover {
    opacity: 0.5;
}
.collapse-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    top: 20px;
    right: -36px;
    background: #E1E1E1;
    border-radius: 0px 12px 12px 0px;
    border: none;
    z-index: 10;
}
.collapse-btn:focus {
    outline: none;
}
.filter-options {
    width: 100px;
    padding: 6px;
    background-color: white;
    top: 0;
    right: -116px;
    z-index: 100;
    border-radius: 6px;
    border: 1px solid #dee2e6;
} 
.filter-option {
    padding: 3px 6px;
    border-radius: 3px;
}

.filter-option:hover {
    color: white;
    background-color: #3d62be;
}
@media (max-width: 576px) { 
    .filter-options {
        top: 0;
        right: 26px;
    }
}
.hide-in-sm {
    display: none !important;;
}
@media (min-width: 992px) { 
    .hide-in-sm {
        display: block !important;; 
    }
}
.form-signin {
    width: 100%;
    height: 90vh;
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
  }
  .form-signin .checkbox {
    font-weight: 400;
  }
  .form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
  .form-signin .form-control:focus {
    z-index: 2;
  }
  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
  
